.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}
    .modal-main {
      position:fixed;
      background: rgb(219, 219, 219);
      width: 80%;
      height: auto;
      top:50%;
      left:50%;
      -webkit-transform: translate(-50%,-50%);
          -ms-transform: translate(-50%,-50%);
              transform: translate(-50%,-50%);
    }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }


